.fourcontainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 12px;
  padding: 17px;
}

.fourcontainer .box img {
  width: 105px;
  height: 95px;
}

.fourcontainer .box {
  font-size: 9px;
  font-weight: 800;
}

.button2 {
  display: flex;
  justify-content: center;
  align-items: center;
